body {

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* { 
  background-color: rgb(90, 90, 90);
}

.app-container {
  display:flex;
  flex-direction: row;
  overflow-x: hidden;
  background-color: black;
  width: 100%;
}

h1 {
  font-size: 2em;
  font-weight: lighter;
  text-align: center;
  color: #cccccc;
  font-family: 'Spicy Rice', cursive;
  text-transform: uppercase;
  margin: 10px;
}


.chat {
  width: 300px;
  height: 320px;
  margin-left: auto; 
  margin-right: auto;
  font-family:tahoma; 
  font-size:12px; 
  padding: 10px;
  color: 	#E8E8E8;
  position: absolute;
  top:10px;
  right: 25px;
  z-index: 1;
  border-radius:2px; 
  background-color: rgba(89, 89, 89, .7);
}

.chat .content { 
  padding: 15px 15px 25px 15px;
  background-color: rgba(100, 100, 100, .4);
  border-radius:5px;
  overflow-y: scroll; 
  border: 1px solid rgba(0, 0, 100, .4);
  margin-top: 10px; 
  height: 200px; 
}

.chat .content .content-message {
  line-height: 12px;
  margin: 3px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 10px;
  background-color: rgba(0, 51, 204, .3);
  border: 1px solid rgba(0, 51, 204, .5);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  padding-left: 7px;
  padding-right: 10px;
  padding-top: 1px;
  padding-bottom: 2px;
}

.chat .chat-input {
  border-radius:2px; 
  border: 1px solid #ccc;
  margin-top: 10px; 
  padding: 5px; 
  width: 98%;
  color: #E8E8E8;
  background-color: rgba(100, 100, 100, .4);
}

.chat .chat-status {
  display:block;
  float:left;
  margin-top:15px; 
  margin-right: 5px;
  background: none;
}

.chat .chat-status-container {
  background: none;
}
.player-character {
  position: absolute;
  font-size: 7px;
  font-weight: bold;

  padding-top: 1px;
  /* box-shadow: inset 0 0 0 1000px rgba(0,0,0,.2); */
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  text-align: center;
}

.down {
  background: url(/static/media/player-character-2.b6156924.png) -16px -640px;
}
.down-moving {
  background: url(/static/media/player-character-2.b6156924.png);
  background-position-y: -640px;
  -webkit-animation: down-direction .5s steps(9) infinite;
          animation: down-direction .5s steps(9) infinite;
}

.up {
  background: url(/static/media/player-character-2.b6156924.png) -16px -520px;
}
.up-moving {
  background: url(/static/media/player-character-2.b6156924.png);
  background-position-y: -520px;
  -webkit-animation: up-direction .5s steps(9) infinite;
          animation: up-direction .5s steps(9) infinite;
}

.left {
  background: url(/static/media/player-character-2.b6156924.png) -16px -576px;
}
.left-moving {
  background: url(/static/media/player-character-2.b6156924.png);
  background-position-y: -576px;
  -webkit-animation: left-direction .5s steps(9) infinite;
          animation: left-direction .5s steps(9) infinite;
}

.right {
  background: url(/static/media/player-character-2.b6156924.png) -16px -704px;
}
.right-moving {
  background: url(/static/media/player-character-2.b6156924.png);
  background-position-y: -704px;
  -webkit-animation: right-direction .5s steps(9) infinite;
          animation: right-direction .5s steps(9) infinite;
}



@-webkit-keyframes up-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}



@keyframes up-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}
@-webkit-keyframes left-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}
@keyframes left-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}
@-webkit-keyframes right-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}
@keyframes right-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}

@-webkit-keyframes down-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}

@keyframes down-direction {
  from { background-position-x: -16px; }
  to { background-position-x: -592px; }
}


[tabindex] {
  outline: none !important;
}
.grid-tile {
  background-color: rgb(116, 38, 14);
  width: 32px;
  height: 32px;
  display: flex;
}

.grid-tile-row {
  display: flex;
  align-items: flex-start;
  flex-wrap: nowrap;
}
.grid {
  width: auto;
  height: 100%;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 10px;
  position:relative;
  margin-left: 14px;
  background-color:black;
}
